<template>
  <transition name="modal">
    <div class="modal-mask" @click="overlayClick">
      <div class="modal-wrapper">
        <div class="modal-container" v-on:click.stop>
          <div class="modal-header">
            <slot name="header">
              <h3 v-if="title">{{title}}</h3>
            </slot>
            <i class="icon-close" v-if="!hideClose" @click="close"></i>
          </div>
          <div class="modal-body">
            <slot name="body">
              <p class="modal-message" v-if="msg">{{msg}}</p>
            </slot>
          </div>
          <div class="modal-footer clear-fix" v-if="!hideFooter">
            <slot name="footer">
              <ex-button type="primary">{{btnText}}</ex-button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import {
  mapActions,
} from 'vuex';

export default {
  name: 'modal',
  props: {
    title: {
      type: String,
      default: '',
    },
    msg: {
      type: String,
      default: '',
    },
    btnText: {
      type: String,
      default: '',
    },
    clickOverlayClose: {
      type: Boolean,
      default: true,
    },
    hideClose: {
      type: Boolean,
      default: false,
    },
    hideFooter: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    ...mapActions({
      close: 'closeModal',
    }),
    overlayClick() {
      if (this.clickOverlayClose) {
        this.close();
      }
    },
  },
  data() {
    return {};
  },
};
</script>
<style>
.modal-mask {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .3s ease;
  display: flex;
  justify-content: center;
}

.modal-wrapper {
  align-self: center;
}

.modal-container {
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  height: 42px;
  border-bottom: 1px solid #e9e9eb;
  text-align: center;
  position: relative;
}

.modal-header h3 {
  display: inline;
  line-height: 42px;
}

.modal-header .icon-close {
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal-body {
  margin: 20px 0;
}

.modal-body .modal-message {
  margin: 0 20px;
  min-height: 100px;
  text-align: center;
  min-width: 250px;
}

.modal-footer {
  text-align: center;
  padding: 0 20px 20px;
}

.modal-default-button {
  float: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>



