const ERROR_CONSTANTS = window.ERROR_CONSTANTS || {};

const util = {
  ERROR_CONSTANTS,
  sumObject(arr) {
    const t = {};
    arr.forEach((a) => {
      for (const key in a) {
        if (t[key] && typeof a[key] === 'number') {
          t[key] += a[key];
        } else if (!t[key]) {
          t[key] = a[key];
        }
      }
    });
    return t;
  },
  getErrorInfo(response) {
    let tips = ERROR_CONSTANTS[response.body.code] || '';
    if (tips.indexOf('#INFO#') !== -1) {
      tips = tips.replace('#INFO#', response.body.addition_error_info);
    }
    return tips;
  },
};

export default util;



