<template>
  <div class="wemedia-container">
    <div class="wemedia-chip">
      <img :src="data.mediaAccount.headImg" />
      <h4>{{data.mediaAccount.screenName}}</h4>
    </div>
    <dl>
      <div class="flex-item stretch">
        <dt>()</dt>
        <dd>{{cost.totalPay / 1000 / 100 | currencyFilter('', 2) || '-'}}</dd>
      </div>
      <div class="flex-item double stretch">
        <dt></dt>
        <dd>
          <a target="_blank" class="amber-text ellipsis" v-if="task.adInfo" :href="task.adInfo.adUrl">{{task.adInfo.adUrl}}</a>
        </dd>
      </div>
      <div class="flex-item">
        <dt>()</dt>
        <dd>{{cost.impNum}}</dd>
      </div>
      <div class="flex-item">
        <dt>()</dt>
        <dd>{{(cost.totalPay / cost.impNum * 1000 / 1000 / 100) | currencyFilter('', 2) || '-'}}</dd>
      </div>
      <div class="flex-item">
        <dt></dt>
        <dd>{{data.status | statusFilter('iv')}}</dd>
      </div>
      <div class="flex-item">
        <dt>()</dt>
        <dd>{{cost.clickNum}}</dd>
      </div>
      <div class="flex-item">
        <dt>()</dt>
        <dd>{{(cost.totalPay / cost.clickNum / 1000 / 100) | currencyFilter('', 2) || '-'}}</dd>
      </div>
      <div class="flex-item">
        <dt></dt>
        <dd v-if="task.adInfo" :class="statusMap[0]">{{task.adInfo.status | adStatusFilter(task.status, task.adInfo.reason)}}</dd>
      </div>
    </dl>
    <div class="wemedia-status" v-show="task.status === 3 || task.status === 4 || task.status === 7">
      <a href="javascript:;" class="amber-text" v-show="task.status === 3" @click="pauseTask"></a>
      <a href="javascript:;" class="amber-text" v-show="task.status === 7 || task.status === 4" @click="startTask"></a>
    </div>
  </div>
</template>
<script>
export default {
  name: 'weMedia',
  props: {
    data: {
      type: Object,
      required: true,
    },
    task: {
      type: Object,
      default() {
        return {
          status: -1,
          adInfo: {
            adUrl: '',
            status: 1,
          },
        };
      },
    },
    cost: {
      type: Object,
      default() {
        return {
          totalPay: 0,
          clickNum: 0,
          impNum: 0,
        };
      },
    },
  },
  methods: {
    pauseTask() {
      this.$store.dispatch('operateTask', {
        operate: 2,
        taskId: this.task.id,
      }).then(() => {
        this.$store.commit({
          type: 'UPDATE_TASK',
          activityId: this.task.activityId,
          taskId: this.task.id,
          status: 4,
        });
      });
    },
    startTask() {
      this.$store.dispatch('operateTask', {
        operate: 3,
        taskId: this.task.id,
      }).then(() => {
        this.$store.commit({
          type: 'UPDATE_TASK',
          activityId: this.task.activityId,
          taskId: this.task.id,
          status: 3,
        });
      });
    },
  },
  data() {
    return {
      statusMap: {
        0: 'amber-text',
        1: 'purple-text',
        2: 'indigo-text',
        3: 'teal-text',
        4: 'red-text',
        5: 'red-text',
      },
    };
  },
};
</script>
<style lang="less">
@import url(../../styles/variable.less);
.wemedia-container {
  margin-top: 20px;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  border: 1px solid @border-color;
  .wemedia-chip {
    min-width: 120px;
    margin-right: 20px;
    text-align: center;
    border-right: 1px solid @border-color;
    >img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      margin-bottom: 20px;
    }
  }
  >dl {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    .flex-item {
      width: 33%;
      &.double {
        width: 66%;
      }
      &.stretch {
        height: 2.5em;
      }
      .ellipsis {
        display: flex;
        overflow-x: hidden;
        max-width: 400px;
      }
    }
    dt,
    dd {
      margin: 0;
      display: inline-block;
    }
  }
  .wemedia-status {
    border-left: 1px solid @border-color;
    min-width: 120px;
    text-align: center;
  }
}
</style>



