<template>
  <ul class="tabs-content">
    <li v-for="(tab, index) in tabs"
      :class="{'current': tab.checked}"
      @click="selectTab(tab)">
      {{tab.name}}
    </li>
  </ul>
</template>
<script>
export default {
  name: 'tabs',
  props: {
    tabs: {
      type: Array,
      required: true,
    },
  },
  methods: {
    selectTab(tab) {
      this.$emit('selectTab', tab);
    },
  },
};
</script>



