<template>
  <form>
    <div class="input-field">
      <label></label>
      <input type="text" v-model="localData.name" placeholder="" />
    </div>
    <div class="input-field">
      <label></label>
      <textarea rows="3" v-model="localData.desc" placeholder=""></textarea>
    </div>
    <div class="input-field">
      <label></label>
      <el-date-picker v-model="localData.startTime" type="datetime" placeholder=""></el-date-picker>
    </div>
    <div class="input-field">
      <label></label>
      <input type="number" v-model.number="localData.budget" placeholder="" step="100" />
      <span></span>
    </div>
    <div class="input-field">
      <label></label>
      <input type="number" v-model.number="localData.minPrice" placeholder="" step="10" />
      <span>-</span>
      <input type="number" v-model.number="localData.maxPrice" placeholder="" step="10" />
      <span>/CPM</span>
    </div>
    <div class="input-field">
      <label></label>
      <ex-button v-if="!bindIdea" type="primary" @click="selectIdea('select', true)"></ex-button>
      <p class="idea-select" v-else>
        <span>{{bindIdea.name}}</span>
        <a href="javascript:;" class="amber-text" @click="selectIdea('inspect')"></a>
        <a href="javascript:;" class="amber-text" @click="selectIdea('select')"></a>
      </p>
    </div>
    <div class="input-field">
      <label></label>
      <ul class="wemedia-list">
        <li v-for="invite in inviteList">
          <contacts class="chip" :data="invite"></contacts>
        </li>
        <li @click="selectWeMedia">
          <contacts class="chip"></contacts>
        </li>
      </ul>
    </div>
    <div class="input-field">
      <label></label>
      <textarea rows="3" v-model="localData.memo" placeholder=""></textarea>
    </div>
    <div class="form-option">
      <ex-button type="primary" @click="operation('publish')"></ex-button>
      <ex-button @click="operation('save')"></ex-button>
    </div>
  </form>
</template>
<script>
import moment from 'moment';
import { mapActions } from 'vuex';
import Contacts from '../widgets/Contacts';

export default {
  name: 'activityForm',
  components: {
    Contacts,
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        name: '',
        budget: 0,
        desc: '',
        minPrice: 0,
        maxPrice: 0,
        memo: '',
        creativeId: '',
        beInviteUser: '',
        startTime: new Date(),
      }),
    },
  },
  computed: {
    bindIdea() {
      return this.$store.state.activity.ideas.find(i =>
        i.id === parseInt(this.localData.creativeId, 10),
      );
    },
  },
  methods: {
    ...mapActions({
      switchIdea: 'switchIdea',
      showSelectIdea: 'showSelectIdea',
      createActivity: 'createActivity',
      updateActivity: 'updateActivity',
      publishActivity: 'publishActivity',
      showSelectWeMedia: 'showSelectWeMedia',
    }),
    operation(type) {
      if (type === 'publish') {
        const now = new Date();
        const st = this.localData.startTime;
        const d = (st.getTime() - now.getTime()) / 1000;
        if (d < 3600) {
          this.$store.dispatch('showToasts', {
            text: '',
          });
          return;
        }
        if (this.bindIdea && this.bindIdea.status === 1) {
          this.$store.dispatch('showToasts', {
            text: '',
          });
          return;
        }
      }
      const params = {
        ...this.localData,
        budget: this.localData.budget * 1000 * 100,
        minPrice: this.localData.minPrice * 100,
        maxPrice: this.localData.maxPrice * 100,
        projectId: this.data.projectId || parseInt(this.$route.query.projectId, 10),
        startTime: `${moment(this.localData.startTime).format('YYYY-MM-DD HH:mm')}:00`,
      };
      let action;
      switch (type) {
        case 'publish':
          action = this.publishActivity(params);
          break;
        case 'save':
          if (this.localData.id) {
            action = this.updateActivity(params);
          } else {
            action = this.createActivity(params);
          }
          break;
        default:
          break;
      }
      action.then(() => {
        this.$router.go(-1);
      });
    },
    ideaPitch(id) {
      this.localData.creativeId = id;
    },
    selectIdea(type, flag) {
      if (flag) {
        this.$store.commit('RESET_IDEA');
      } else {
        this.switchIdea(this.bindIdea);
      }
      this.showSelectIdea({
        type,
        callback: this.ideaPitch,
      });
    },
    selectWeMedia() {
      this.showSelectWeMedia({
        inviteList: this.inviteList,
        callback: this.invitePitch,
      });
    },
    invitePitch(selectList) {
      this.inviteList = selectList;
      this.localData.beInviteUser = selectList.map(s => s.uid).join(',');
    },
  },
  data() {
    return {
      inviteList: this.$store.state.activity.draftInfo.mediaList.slice(),
      localData: {
        ...this.data,
        startTime: new Date(this.data.startTime),
        budget: this.data.budget / 1000 / 100,
        minPrice: this.data.minPrice / 100,
        maxPrice: this.data.maxPrice / 100,
      },
    };
  },
};
</script>



