<template>
  <transition name="toasts">
    <div class="toasts-content" v-show="config.show">{{config.text}}</div>
  </transition>
</template>
<script>
export default {
  name: 'toasts',
  computed: {
    config() {
      return this.$store.state.toasts.config;
    },
  },
};
</script>



