import Vue from 'vue';
import * as types from '../mutation-types';
import api from '../../services/request';

/* eslint-disable no-shadow */

// 

const state = {
  ideas: [],
  projects: [],
  activities: [],
  weMediaList: [],
  activityInfo: {},
  draftInfo: {},
};

const getters = {
  allDetail: state => state.activityInfo,
  allProjects: state => state.projects,
  getCurIdea: state => state.ideas.find(i => i.checked),
  getCurProject: state => state.projects.find(p => p.checked),
  frozen: (state) => {
    const filArr = state.activities.filter(a => a.status !== 1 && a.status !== 5);
    if (filArr.length !== 0) {
      return filArr.reduce((pre, nex) => pre + nex.budget, 0);
    }
    return 0;
  },
  cost: (state) => {
    const filArr = state.activities.filter(a => a.status === 5);
    if (filArr.length !== 0) {
      return filArr.reduce((pre, nex) => pre + nex.cost, 0);
    }
    return 0;
  },
};

const mutations = {
  [types.RECEIVE_IDEA](state, ideas) {
    ideas.forEach((i) => {
      Vue.set(i, 'checked', false);
    });
    state.ideas = ideas;
  },
  [types.RECEIVE_PROJECT](state, projects) {
    projects.forEach((p) => {
      Vue.set(p, 'checked', false);
    });
    state.projects = projects.reverse();
  },
  [types.RECEIVE_ACTIVITY](state, activities) {
    activities.forEach((a) => {
      Vue.set(a, 'taskCostList', []);
      Vue.set(a, 'tasks', []);
    });
    state.activities = activities;
  },
  [types.RECEIVE_ACTIVITY_INFO](state, activityInfo) {
    state.activityInfo = activityInfo;
  },
  [types.RECEIVE_DRAFT_INFO](state, draftInfo) {
    if (!draftInfo.mediaList) {
      Vue.set(draftInfo, 'mediaList', []);
    }
    state.draftInfo = draftInfo;
  },
  [types.RECEIVE_WEMEDIA_LIST](state, weMediaList) {
    state.weMediaList = weMediaList;
  },
  [types.RECEIVE_TASKS_INFO](state, { tasks, id }) {
    const index = state.activities.findIndex(a => a.id === id);
    state.activities[index].tasks = tasks;
  },
  [types.RECEIVE_TASK_COST](state, { taskCostList, id }) {
    const index = state.activities.findIndex(a => a.id === id);
    state.activities[index].taskCostList = taskCostList;
  },
  [types.CREATE_IDEA](state, idea) {
    Vue.set(idea, 'checked', false);
    state.ideas.push(idea);
  },
  [types.CREATE_PROJECT](state, project) {
    Vue.set(project, 'checked', false);
    state.projects.push(project);
  },
  [types.CREATE_ACTIVITY](state, activity) {
    Vue.set(activity, 'taskCostList', []);
    Vue.set(activity, 'tasks', []);
    state.activities.push(activity);
  },
  [types.UPDATE_IDEA](state, idea) {
    state.ideas.forEach((i, index) => {
      if (i.id === idea.id) {
        Object.keys(idea).forEach((key) => {
          if (key !== 'projectId') {
            state.ideas[index][key] = idea[key];
          }
        });
      }
    });
  },
  [types.UPDATE_ACTIVITY](state, activity) {
    state.activities.forEach((a, i) => {
      if (a.id === activity.id) {
        Object.keys(activity).forEach((key) => {
          // getterVue.set
          if (key !== 'projectId') {
            state.activities[i][key] = activity[key];
          }
        });
      }
    });
    // @JerryChen activityInfo
    Object.keys(activity).forEach((key) => {
      if (key !== 'projectId' && state.activityInfo.activity[key]) {
        state.activityInfo.activity[key] = activity[key];
      }
    });
  },
  [types.UPDATE_PROJECT](state, project) {
    state.projects.forEach((p, i) => {
      if (p.id === project.id) {
        state.projects[i].name = project.name;
        state.projects[i].budget = project.budget;
        state.projects[i].memo = project.memo;
      }
    });
  },
  [types.RESET_IDEA](state) {
    state.ideas.forEach((i) => {
      i.checked = false;
    });
  },
  [types.SWITCH_IDEA](state, idea) {
    state.ideas.forEach((i) => {
      if (i.id === idea.id) {
        i.checked = true;
      } else {
        i.checked = false;
      }
    });
  },
  [types.SWITCH_PROJECT](state, project) {
    state.projects.forEach((p) => {
      if (p.id === project.id) {
        p.checked = true;
      } else {
        p.checked = false;
      }
    });
  },
  [types.PICK_WEMEDIA](state, payload) {
    state.weMediaList.forEach((w) => {
      if (payload.idList.indexOf(w.id) !== -1) {
        w.checked = true;
      } else {
        w.checked = false;
      }
    });
  },
  [types.END_ACTIVITY](state, activityId) {
    state.activities.find(a => a.id === activityId).status = 4;
  },
  [types.UPDATE_TASK](state, { activityId, taskId, status }) {
    state.activities.find(a => a.id === activityId).tasks.forEach((t) => {
      if (t.id === taskId) {
        t.status = status;
      }
    });
  },
};

const actions = {
  getIdeas({ commit }, id) {
    return new Promise((resolve) => {
      api.getIdeas(id).then((response) => {
        if (response.body.code === 1) {
          commit(types.RECEIVE_IDEA, response.body.result);
          setTimeout(() => {
            resolve();
          }, 1000);
        }
      });
    });
  },
  getProjects({ commit }) {
    api.getProjects().then((response) => {
      if (response.body.code === 1) {
        const result = response.body.result;
        commit(types.RECEIVE_PROJECT, result);
        if (result !== 0) {
          commit(types.SWITCH_PROJECT, result[0]);
        }
      }
    });
  },
  getActivities({ commit }, id) {
    return new Promise((resolve) => {
      api.getActivities(id).then((response) => {
        if (response.body.code === 1) {
          commit(types.RECEIVE_ACTIVITY, response.body.result);
          setTimeout(() => {
            resolve();
          }, 1000);
          response.body.result.forEach((r) => {
            api.getTasksInfo(r.id).then((response) => {
              if (response.body.code === 1) {
                if (response.body.tasks) {
                  commit(types.RECEIVE_TASKS_INFO, {
                    tasks: response.body.tasks,
                    id: r.id,
                  });
                }
                if (response.body.taskCostList) {
                  commit(types.RECEIVE_TASK_COST, {
                    taskCostList: response.body.taskCostList,
                    id: r.id,
                  });
                }
              }
            });
          });
        }
      });
    });
  },
  getActivityInfo({ commit }, activityId) {
    return new Promise((resolve) => {
      api.getActivityInfo(activityId).then((response) => {
        if (response.body.code === 1) {
          commit(types.RECEIVE_ACTIVITY_INFO, response.body);
          commit(types.UPDATE_ACTIVITY, response.body.activity);
          api.getTasksInfo(activityId).then((response) => {
            if (response.body.code === 1) {
              if (response.body.tasks) {
                commit(types.RECEIVE_TASKS_INFO, {
                  tasks: response.body.tasks,
                  id: activityId,
                });
              }
              if (response.body.taskCostList) {
                commit(types.RECEIVE_TASK_COST, {
                  taskCostList: response.body.taskCostList,
                  id: activityId,
                });
              }
              setTimeout(() => {
                resolve();
              }, 1000);
            }
          });
        }
      });
    });
  },
  getDraftInfo({ commit }, activityId) {
    return new Promise((resolve) => {
      api.getDraftInfo(activityId).then((response) => {
        if (response.body.code === 1) {
          commit(types.RECEIVE_DRAFT_INFO, response.body);
          setTimeout(() => {
            resolve();
          }, 1000);
        }
      });
    });
  },
  getWeMediaList({ commit }, { idList, locationCode = 0 }) {
    return new Promise((resolve) => {
      api.getWeMediaList(locationCode).then((response) => {
        if (response.body.code === 1) {
          const inviteList = response.body.result;
          inviteList.forEach((i) => {
            if (idList.indexOf(i.id) !== -1) {
              Object.assign(i, { checked: true });
            } else {
              Object.assign(i, { checked: false });
            }
          });
          commit(types.RECEIVE_WEMEDIA_LIST, inviteList);
          resolve();
        }
      });
    });
  },
  switchIdea({ commit }, idea) {
    return new Promise((resolve) => {
      commit(types.SWITCH_IDEA, idea);
      resolve();
    });
  },
  switchProject({ commit }, project) {
    commit(types.SWITCH_PROJECT, project);
  },
  createIdea({ commit, dispatch }, idea) {
    return new Promise((resolve) => {
      api.createIdea(idea).then((response) => {
        if (response.body.code === 1) {
          const obj = {
            ...idea,
            id: response.body.id,
            status: 0,
          };
          commit(types.CREATE_IDEA, obj);
          dispatch('showToasts', {
            text: '',
          });
          resolve();
        }
      });
    });
  },
  createProject({ commit, dispatch }, project) {
    api.createProject(project).then((response) => {
      if (response.body.code === 1) {
        Object.assign(project, { id: response.body.id });
        commit(types.CREATE_PROJECT, project);
        commit(types.CLOSE_MODAL);
        dispatch('showToasts', {
          text: '',
        });
      }
    });
  },
  createActivity({ commit, dispatch }, activity) {
    return new Promise((resolve) => {
      api.createActivity(activity).then((response) => {
        if (response.body.code === 1) {
          const obj = {
            ...activity,
            id: response.body.id,
            status: 1,
          };
          commit(types.CREATE_ACTIVITY, obj);
          dispatch('showToasts', {
            text: '',
          });
          resolve();
        }
      });
    });
  },
  updateIdea({ commit, dispatch }, idea) {
    return new Promise((resolve) => {
      api.updateIdea(idea).then((response) => {
        if (response.body.code === 1) {
          const obj = {
            ...idea,
            status: 0,
          };
          commit(types.UPDATE_IDEA, obj);
          dispatch('showToasts', {
            text: '',
          });
          resolve();
        }
      });
    });
  },
  updateProject({ commit, dispatch }, project) {
    api.updateProject(project).then((response) => {
      if (response.body.code === 1) {
        commit(types.UPDATE_PROJECT, project);
        commit(types.CLOSE_MODAL);
        dispatch('showToasts', {
          text: '',
        });
      }
    });
  },
  updateActivity({ commit, dispatch }, activity) {
    return new Promise((resolve) => {
      api.updateActivity(activity).then((response) => {
        if (response.body.code === 1) {
          const obj = {
            ...activity,
            startTime: Date.parse(new Date(activity.startTime)),
          };
          commit(types.UPDATE_ACTIVITY, obj);
          dispatch('showToasts', {
            text: '',
          });
          resolve();
        }
      });
    });
  },
  publishActivity({ commit, dispatch }, activity) {
    return new Promise((resolve) => {
      api.publishActivity(activity).then((response) => {
        if (response.body.code === 1) {
          if (activity.id) {
            const obj = {
              ...activity,
              status: 2,
            };
            commit(types.UPDATE_ACTIVITY, obj);
          } else {
            const obj = {
              ...activity,
              id: response.body.id,
              startTime: Date.parse(new Date(activity.startTime)),
              status: 2,
            };
            commit(types.CREATE_ACTIVITY, obj);
          }
          dispatch('showToasts', {
            text: '',
          });
          resolve();
        }
      });
    });
  },
  endActivity({ commit, dispatch }, activityId) {
    api.endActivity(activityId).then((response) => {
      if (response.body.code === 1) {
        commit(types.END_ACTIVITY, activityId);
      }
    });
  },
  operateTask({ commit }, { operate, taskId }) {
    return new Promise((resolve) => {
      api.operateTask(operate, taskId).then((response) => {
        if (response.body.code === 1) {
          resolve();
        }
      });
    });
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};



