<template>
  <div class="card-list">
    <div class="title">
      <h3><i class="icon-idea"></i></h3>
      <a class="amber-text" href="javascript:;" @click="modify()">+ </a>
    </div>
    <transition-group name="list" tag="ul" @enter="enter" v-if="loading" appear>
      <li v-for="(idea, index) in ideas"
      :key="idea.id"
      :data-index="index + 1"
      @click="modify(idea)">
        <idea-card :data="idea"></idea-card>
      </li>
    </transition-group>
    <loading height="450" v-else></loading>
    <place-holder v-if="ideas.length === 0 && loading" prompt=""></place-holder>
  </div>
</template>
<script>
import { mapActions } from 'vuex';
import IdeaCard from '../widgets/IdeaCard';
import PlaceHolder from '../widgets/PlaceHolder';

export default {
  name: 'ideas',
  props: ['id'],
  components: {
    IdeaCard,
    PlaceHolder,
  },
  computed: {
    ideas() {
      return this.$store.state.activity.ideas;
    },
  },
  methods: {
    ...mapActions({
      init: 'getIdeas',
      switchIdea: 'switchIdea',
      showSelectIdea: 'showSelectIdea',
    }),
    modify(idea) {
      if (idea) {
        if (idea.status === 1) {
          this.$router.push({
            path: `/activity/newidea?id=${idea.id}`,
          });
        } else {
          this.switchIdea(idea).then(() => {
            this.showSelectIdea({ type: 'inspect' });
          });
        }
      } else {
        this.$router.push({
          path: `/activity/newidea?projectId=${this.id}`,
        });
      }
    },
    enter(el, done) {
      const delay = el.dataset.index * 150;
      el.style.transitionDelay = `${delay}ms`;
      el.style.WebkitTransitionDelay = `${delay}ms`;
      setTimeout(() => {
        done();
      }, delay);
    },
  },
  watch: {
    id(val) {
      this.loading = false;
      this.init(val).then(() => {
        this.loading = true;
      });
    },
  },
  created() {
    this.init(this.id).then(() => {
      this.loading = true;
    });
  },
  data() {
    return {
      loading: false,
    };
  },
};
</script>



