const statusMapA = {
  // 0
  0: '',
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
};
const statusMapI = {
  0: '',
  1: '',
  2: '',
};
const statusMapT = {
  '-1': '',
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '',
  7: '',
  8: '',
  71: '',
  72: '',
  73: '',
  74: '',
};
const statusMapIV = {
  '-1': '',
  0: '',
  1: '',
  2: '',
  3: '',
};
export default function statusFilter(value, type) {
  let output;
  switch (type) {
    case 'a':
      output = statusMapA[value];
      break;
    case 'i':
      output = statusMapI[value];
      break;
    case 't':
      output = statusMapT[value];
      break;
    case 'iv':
      output = statusMapIV[value];
      break;
    default:
      output = statusMapA[value];
      break;
  }
  return output;
}



