<template>
  <div class="account-journal">
    <a href="javascript:;" class="amber-text"></a>
    <table>
      <thead>
        <tr>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{{userInfo.totalOut / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{userInfo.frozen / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{userInfo.money / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{(userInfo.money - userInfo.frozen) / 1000 / 100 | currencyFilter('') || '0'}}</td>
        </tr>
      </tbody>
    </table>
     <div class="data-filter">
      <div class="input-field">
        <label></label>
        <el-date-picker v-model="daterange" type="daterange" placeholder=""></el-date-picker>
      </div>
      <ex-button type="primary" @click="query()"></ex-button>
    </div>
    <h3></h3>
    <table>
      <thead>
        <tr>
          <th></th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="fin in finance" :key="fin.createTime">
          <td>{{fin.createTime | timeFilter('YYYY-MM-DD') || '-'}}</td>
          <td>{{fin.deposit / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{fin.credit / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{fin.balance / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{fin.memo || '-'}}</td>
        </tr>
      </tbody>
    </table>
    <pagination :curPage="curPage" @prevPage="prevPage" @nextPage="nextPage"></pagination>
  </div>
</template>
<script>
import moment from 'moment';
import { mapActions } from 'vuex';
import Pagination from '../widgets/Pagination';

export default {
  name: 'account',
  components: {
    Pagination,
  },
  computed: {
    finance() {
      return this.$store.state.account.finance.concat().reverse();
    },
    userInfo() {
      return this.$store.state.account.userInfo;
    },
  },
  methods: {
    ...mapActions({
      fetchData: 'getFinance',
      getUserInfo: 'getUserInfo',
    }),
    prevPage() {
      this.curPage -= 1;
      this.query();
    },
    nextPage() {
      this.query(this.curPage + 1, () => {
        this.curPage += 1;
      });
    },
    query(page, callback) {
      const params = {
        startTime: this.daterange ? moment(this.daterange[0]).format('YYYY-MM-DD') : undefined,
        endTime: this.daterange ? moment(this.daterange[1]).format('YYYY-MM-DD') : undefined,
        page: page || this.curPage,
      };
      this.fetchData(params).then(() => {
        if (typeof callback === 'function') {
          callback();
        }
      });
    },
  },
  created() {
    this.query();
    this.getUserInfo();
  },
  data() {
    return {
      curPage: 1,
      daterange: '',
    };
  },
};
</script>
<style lang="less">
@import url('../../styles/variable.less');
.account-journal {
  background-color: @white;
  max-height: 800px;
  padding-bottom: 53px;
  min-height: 500px;
  position: relative;
  >h3 {
    text-align: center;
    padding-top: 20px;
  }
  >a.amber-text {
    display: block;
    padding: 20px;
    padding-bottom: 0;
    text-align: right;
  }
  table {
    margin: 20px;
    width: 960px;
  }
  .data-filter {
    display: flex;
    .input-field {
      margin-left: 20px;
    }
    a.button {
      margin-top: 20px;
      margin-left: 20px;
    }
  }
  .pagination {
    position: absolute;
    bottom: 20px;
  }
}
</style>



