<template>
  <div class="data-center">
    <tabs :tabs="tabs" @selectTab="switchTab"></tabs>
    <div class="data-filter">
      <div class="input-field">
        <label></label>
        <el-date-picker v-model="daterange" type="daterange" placeholder=""></el-date-picker>
      </div>
      <div class="input-field" v-show="curTab.name !== ''">
        <label>{{curTab.name}}</label>
        <input type="text" :placeholder="curTab.name" v-model="search" />
      </div>
      <ex-button type="primary" @click="query()"></ex-button>
    </div>
    <table>
      <thead>
        <tr>
          <th></th>
          <th v-show="curTab.name !== ''">{{curTab.name}}</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data in statis" :key="data">
          <td>{{data.statisDate || '-'}}</td>
          <td v-show="curTab.name !== ''">{{data.name || '-'}}</td>
          <td>{{data.monitorNum || '0'}}</td>
          <td>{{data.clickNum || '0'}}</td>
          <td>{{(data.cost / data.monitorNum * 1000 / 1000 / 100) | currencyFilter('') || '0'}}</td>
          <td>{{(data.cost / data.clickNum / 1000 / 100) | currencyFilter('') || '0'}}</td>
          <td>{{data.cost / 1000 / 100 | currencyFilter('') || '0'}}</td>
        </tr>
      </tbody>
    </table>
    <pagination :curPage="curPage" @prevPage="prevPage" @nextPage="nextPage"></pagination>
  </div>
</template>
<script>
import moment from 'moment';
import { mapActions } from 'vuex';
import Tabs from '../widgets/Tabs';
import Pagination from '../widgets/Pagination';

export default {
  name: 'data',
  components: {
    Tabs,
    Pagination,
  },
  computed: {
    tabs() {
      return this.$store.state.data.tabs;
    },
    curTab() {
      return this.$store.getters.curTab;
    },
    statis() {
      return this.$store.state.data.statis;
    },
  },
  methods: {
    ...mapActions({
      fetchData: 'getStatis',
    }),
    switchTab(tab) {
      this.$store.commit({
        type: 'SWITCH_TAB',
        tab,
      });
      this.search = '';
      this.curPage = 1;
      this.query();
    },
    prevPage() {
      this.curPage -= 1;
      this.query();
    },
    nextPage() {
      this.query(this.curPage + 1, () => {
        this.curPage += 1;
      });
    },
    query(page, callback) {
      const params = {
        startTime: this.daterange ? moment(this.daterange[0]).format('YYYY-MM-DD') : undefined,
        endTime: this.daterange ? moment(this.daterange[1]).format('YYYY-MM-DD') : undefined,
        name: this.search ? this.search : undefined,
        page: page || this.curPage,
        type: this.curTab.type,
      };
      this.fetchData(params).then(() => {
        if (typeof callback === 'function') {
          callback();
        }
      });
    },
  },
  created() {
    this.query();
  },
  data() {
    return {
      curPage: 1,
      daterange: '',
      search: '',
    };
  },
};
</script>
<style lang="less">
@import url('../../styles/variable.less');
.data-center {
  background-color: @white;
  max-height: 800px;
  padding-bottom: 53px;
  min-height: 500px;
  position: relative;
  table {
    margin: 20px;
    width: 960px;
  }
  .data-filter {
    display: flex;
    .input-field {
      margin-left: 20px;
    }
    a.button {
      margin-top: 20px;
      margin-left: 20px;
    }
  }
  .pagination {
    position: absolute;
    bottom: 20px;
  }
}
</style>



