<template>
  <modal id="new-project" :click-overlay-close="false">
    <h3 slot="header">{{config.type === 'new' ? '' : ''}}</h3>
    <form slot="body">
      <div class="input-field">
        <label></label>
        <input type="text" v-model="name" placeholder="" />
      </div>
      <div class="input-field">
        <label></label>
        <input type="number" v-model.number="budget" step="100" />
      </div>
      <div class="input-field">
        <label></label>
        <textarea rows="3" v-model="memo" placeholder=""></textarea>
      </div>
    </form>
    <ex-button slot="footer" type="primary" @click="confirm"></ex-button>
  </modal>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'newProject',
  props: ['config'],
  computed: {
    ...mapGetters({
      curProject: 'getCurProject',
    }),
  },
  methods: {
    ...mapActions({
      createProject: 'createProject',
      updateProject: 'updateProject',
    }),
    confirm() {
      if (this.curProject && this.config.type !== 'new') {
        this.updateProject({
          id: this.curProject.id,
          name: this.name,
          budget: this.budget * 1000 * 100,
          memo: this.memo,
        });
      } else {
        this.createProject({
          name: this.name,
          budget: this.budget * 1000 * 100,
          memo: this.memo,
        });
      }
    },
  },
  created() {
    if (this.curProject && this.config.type === 'modify') {
      this.name = this.curProject.name;
      this.memo = this.curProject.memo;
      this.budget = this.curProject.budget / 1000 / 100;
    } else {
      this.name = '';
      this.memo = '';
      this.budget = 0;
    }
  },
  data() {
    return {
      name: '',
      memo: '',
      budget: 0,
    };
  },
};
</script>
<style lang="less">
#new-project {
  .modal-container {
    width: 450px;
  }
  .modal-header {
    text-align: left;
    padding-left: 12px;
  }
  .modal-body {
    margin: 20px;
  }
  .input-field {
    >label {
      width: 5em;
      vertical-align: top;
    }
    >input, >textarea {
      width: 315px;
    }
  }
}
</style>



