<template>
  <div>
    <transition name="slide-right">
      <div v-show="name === 'activity'">
        <project></project>
        <detail></detail>
      </div>
    </transition>
    <transition name="slide-left">
      <router-view class="activity-router-view clear-fix"></router-view>
    </transition>
  </div>
</template>
<script>
import Project from './Project';
import Detail from './Detail';

export default {
  name: 'activity',
  components: {
    Project,
    Detail,
  },
  computed: {
    name() {
      return this.$route.name;
    },
  },
};
</script>
<style lang="less">
@import url('../../styles/variable.less');
.activity-router-view {
  width: 968px;
  padding: 20px 16px;
  background-color: @white;
  form {
    .input-field {
      >label {
        width: 5em;
        vertical-align: top;
      }
      >input[type=text] {
        width: 400px;
        &.medium {
          width: 200px;
        }
        &.small {
          width: 100px;
        }
      }
      >textarea {
        width: 870px;
      }
    }
  }
  .title {
    text-align: center;
    height: 32px;
    line-height: 32px;
    position: relative;
    >a.amber-text {
      float: left;
      display: block;
      line-height: 16px;
      vertical-align: middle;
      margin: 8px 0;
    }
    >a.button {
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  .form-option {
    margin-top: 20px;
    margin-left: 73px;
  }
}

.slide-left-leave-active {
  position: absolute;
}

.slide-left-enter-active {
  transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
  transition-delay: 0.3s;
}

.slide-left-leave-active {
  transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
}

.slide-left-enter,
.slide-left-leave-active {
  transform: translateX(20px);
  opacity: 0;
}

.slide-right-enter-active {
  transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
  transition-delay: 0.3s;
}

.slide-right-leave-active {
  transition: all 0.3s cubic-bezier(0.42, 0, 0.58, 1);
}

.slide-right-enter,
.slide-right-leave-active {
  transform: translateX(-20px);
  opacity: 0;
}
</style>



