import * as types from '../mutation-types';

/* eslint-disable no-shadow */

const state = {
  alert: {
    show: false,
  },
  confirm: {
    show: false,
  },
  newProject: {
    show: false,
    type: 'new',
  },
  selectWeMedia: {
    show: false,
    inviteList: [],
    callback: () => {},
  },
  selectIdea: {
    show: false,
    type: 'select',
    callback: () => {},
  },
};

const getters = {
  getAlert: state => state.alert,
  getConfirm: state => state.confirm,
  getNewProject: state => state.newProject,
  getSelectIdea: state => state.selectIdea,
  getSelectWeMedia: state => state.selectWeMedia,
};

const actions = {
  showAlert({ commit }, config) {
    commit(types.SHOW_ALERT, config);
  },
  showConfirm({ commit }, config) {
    commit(types.SHOW_CONFIRM, config);
  },
  showNewProject({ commit }, config) {
    Object.assign(config, { show: true });
    commit(types.SHOW_NEW_PROJECT, config);
  },
  showSelectIdea({ commit }, config) {
    Object.assign(config, { show: true });
    commit(types.SHOW_SELECT_IDEA, config);
  },
  showSelectWeMedia({ commit, dispatch }, config) {
    Object.assign(config, { show: true });
    commit(types.SHOW_SELECT_WEMEDIA, config);
  },
  closeModal({ commit }) {
    commit(types.CLOSE_MODAL);
  },
};

const mutations = {
  [types.SHOW_ALERT](state, config) {
    state.alert = { ...state.alert, ...config };
  },
  [types.SHOW_CONFIRM](state, config) {
    state.confirm = { ...state.confirm, ...config };
  },
  [types.SHOW_NEW_PROJECT](state, config) {
    state.newProject = { ...state.newProject, ...config };
  },
  [types.SHOW_SELECT_IDEA](state, config) {
    state.selectIdea = { ...state.selectIdea, ...config };
  },
  [types.SHOW_SELECT_WEMEDIA](state, config) {
    state.selectWeMedia = { ...state.selectWeMedia, ...config };
  },
  [types.CLOSE_MODAL](state) {
    Reflect.ownKeys(state).forEach((key) => {
      if (state[key].show) {
        state[key].show = false;
      }
    });
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};



