<template>
  <div>
    <div class="img-overlay">
      {{`${percentage}%`}}
    </div>
    <div class="img-mask"
      :style="{'height': `${100 - percentage}%`}"
      @click="uploadImage()">
      {{percentage ? `${percentage}%` : ''}}
    </div>
    <img :src="image" />
    <!-- TODO: -->
    <!-- <div style="display:none"></div> -->
  </div>
</template>
<script>
export default {
  name: 'imageUpload',
  props: {
    file: {
      type: File,
      required: true,
    },
  },
  methods: {
    progress(e) {
      const percentage = Math.floor((e.loaded / e.total) * 100);
      this.percentage = percentage;
      // if (percentage === 100 && this.hint === '') {
      //   this.$emit('accomplish', this.result);
      // }
    },
    createImage(file) {
      if (typeof FileReader === 'undefined') {
        // TODO: add hint when not support
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        this.image = e.target.result;
        this.uploadImage();
      };
    },
    uploadImage() {
      const fd = new FormData();
      fd.append('file', this.file);
      this.$http.post('/god/creative/pic/upload', fd, {
        progress: this.progress,
      }).then((response) => {
        if (response.body.code === 1) {
          this.hint = '';
          this.result = response.body.result;
          this.$emit('accomplish', this.result);
        }
      });
      // TODO: 
    },
  },
  created() {
    this.createImage(this.file);
  },
  data() {
    return {
      result: null,
      image: '',
      status: 1, // TODO: 1:2:3:
      hint: '',
      percentage: 0,
    };
  },
};
</script>



