<template>
  <div id="activity-form" v-if="loading">
    <div class="title">
      <router-link class="amber-text icon-arrow-right" to="/activity"></router-link>
      <h3>{{id ? '' : ''}}</h3>
    </div>
    <activity-form :data="id ? draft : undefined"></activity-form>
  </div>
  <loading v-else></loading>
</template>
<script>
import { mapActions } from 'vuex';
import ActivityForm from './ActivityForm';

export default {
  name: 'newActivity',
  components: {
    ActivityForm,
  },
  computed: {
    draft() {
      return this.$store.state.activity.draftInfo.activity;
    },
  },
  methods: {
    ...mapActions({
      init: 'getDraftInfo',
    }),
  },
  created() {
    if (this.id) {
      this.init(this.id).then(() => {
        this.loading = true;
      });
    } else {
      this.$store.commit({
        type: 'RECEIVE_DRAFT_INFO',
        draftInfo: {
          activity: {},
          mediaList: [],
        },
      });
      this.loading = true;
    }
  },
  data() {
    return {
      loading: false,
      id: parseInt(this.$route.query.id, 10),
    };
  },
};
</script>
<style lang="less">
#activity-form {
  .wemedia-list {
    width: 870px;
    display: inline-flex;
    >li {
      & + li {
        margin-left: 20px;
      }
    }
  }
  .idea-select > a {
    margin-left: 20px;
  }
}
</style>



