<template>
  <dl class="card-wrapper ripple">
    <dt class="card-row">
      <h3>{{data.name}}</h3>
      <span class="card-status" :class="statusMap[data.status]">{{data.status | statusFilter('a')}}</span>
    </dt>
    <dd class="card-row">{{data.desc || ''}}</dd>
    <div class="card-row-half">
      <dt></dt>
      <dd>{{cost.impNum || '-'}}</dd>
    </div>
    <div class="card-row-half">
      <dt></dt>
      <dd>{{cost.clickNum || '-'}}</dd>
    </div>
    <div class="card-row-half">
      <dt></dt>
      <dd>{{data.budget / 1000 / 100 | currencyFilter('', 0) || '-'}}</dd>
    </div>
    <div class="card-row-half">
      <dt></dt>
      <dd>{{cost.totalPay / 1000 / 100 | currencyFilter('') || '-'}}</dd>
    </div>
  </dl>
</template>
<script>
import util from '../../utils/util';

export default {
  name: 'activityCard',
  props: ['data'],
  computed: {
    curActivity() {
      return this.$store.state.activity.activities.find(a => a.id === this.data.id);
    },
    cost() {
      return util.sumObject(this.curActivity.taskCostList);
    },
  },
  data() {
    return {
      statusMap: {
        0: 'amber',
        1: 'purple',
        2: 'indigo',
        3: 'teal',
        4: 'amber',
        5: 'red',
      },
    };
  },
};
</script>



