<template>
  <form>
    <div class="input-field">
      <label></label>
      <input type="text" v-model="localData.name" placeholder="" />
    </div>
    <div class="input-field">
      <label></label>
      <textarea rows="3" v-model="localData.desc" placeholder=""></textarea>
    </div>
    <div class="input-field">
      <label></label>
      <textarea rows="3" v-model="localData.requires" placeholder=""></textarea>
    </div>
    <div class="input-field">
      <label></label>
      <label for="image-upload">
        <ex-button type="primary"></ex-button>
      </label>
      <input type="file" id="image-upload" name="file" style="visibility: hidden;" multiple @change="imageSelect" />
      <div class="idea-img-list">
        <div class="idea-img-item" v-for="image in renderImg" v-if="image !== ''">
          <i class="icon-close-small" @click="delImage(image)"></i>
          <img :src="image" />
        </div>
        <div class="idea-img-item" v-for="(file, index) in files">
          <i class="icon-close-small" @click="delImage(index)"></i>
          <image-upload :file="file" :key="file.name" @accomplish="uploadComplate($event, index)"></image-upload>
        </div>
      </div>
    </div>
    <div class="input-field">
      <label></label>
      <input type="text" v-model="localData.videoUrl" placeholder="" />
    </div>
    <div class="form-option">
      <ex-button type="primary" @click="submit"></ex-button>
      <ex-button @click="cancel"></ex-button>
    </div>
  </form>
</template>
<script>
import { mapActions } from 'vuex';
import ImageUpload from './ImageUpload';

export default {
  name: 'ideaForm',
  props: {
    data: {
      type: Object,
      default: () => ({
        name: '',
        desc: '',
        requires: '',
        videoUrl: '',
        picIds: '',
      }),
    },
  },
  components: {
    ImageUpload,
  },
  methods: {
    ...mapActions({
      createIdea: 'createIdea',
      updateIdea: 'updateIdea',
      showToasts: 'showToasts',
    }),
    submit() {
      const params = {
        ...this.localData,
        projectId: this.data.projectId || parseInt(this.$route.query.projectId, 10),
        picIds: this.renderImg.join(','),
      };
      let action;
      if (this.localData.id) {
        action = this.updateIdea(params);
      } else {
        action = this.createIdea(params);
      }
      action.then(() => {
        this.$router.go(-1);
      });
    },
    cancel() {
      this.$router.go(-1);
    },
    imageSelect(e) {
      const files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        return;
      }
      const leng = files.length;
      for (let i = 0; i < leng; i += 1) {
        this.files.push(files[i]);
      }
    },
    delImage(imgOrIndex) {
      if (typeof imgOrIndex === 'number') {
        this.files.splice(imgOrIndex, 1);
      } else {
        this.renderImg.splice(this.renderImg.findIndex(r => r === imgOrIndex), 1);
      }
    },
    uploadComplate(imgUrl, index) {
      this.renderImg.push(imgUrl);
      this.files.splice(index, 1);
    },
  },
  data() {
    return {
      files: [],
      localData: Object.assign({}, this.data),
      renderImg: this.data.picIds.split(',').filter(r => r !== ''),
    };
  },
};
</script>



