<template>
  <div class="contacts-container">
    <i v-if="!data.mogeko" class="icon-close-small" @click="close(data.id)"></i>
    <img v-if="!data.mogeko" class="contacts-avatar" :src="data.headImg" />
    <div v-if="data.mogeko" class="add-wemedia">
      <i class="icon-push">+</i>
    </div>
    <h4 class="contacts-name" :class="{mogeko: data.mogeko}">{{data.screenName}}</h4>
    <span class="contacts-location">{{data.location}}</span>
  </div>
</template>
<script>
export default {
  name: 'contacts',
  props: {
    data: {
      type: Object,
      default: () => ({
        headImg: '',
        screenName: '',
        location: '',
        mogeko: true,
      }),
    },
  },
  methods: {
    close(id) {
      this.$emit('deselection', id);
    },
  },
};
</script>
<style lang="less">
@import url(../../styles/variable.less);
.contacts-container {
  &.chip {
    transition: box-shadow 0.3s;
    position: relative;
    width: 132px;
    height: 120px;
    border: 1px solid @border-color;
    border-radius: 5px;
    text-align: center;
    .add-wemedia {
      position: relative;
      cursor: pointer;
      width: 48px;
      height: 48px;
      border: 1px solid @border-color;
      margin: 20px auto 10px;
      border-radius: 50%;
      >.icon-push {
        // css
        display: block;
        font-style: normal;
        color: @border-color-darken;
        font-size: 40px;
        position: absolute;
        top: -4px;
        left: -1px;
        width: 50px;
        height: 50px;
        line-height: 50px;
        font-weight: lighter;
      }
    }
    .contacts-avatar {
      margin-top: 20px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
    .contacts-name {
      &.mogeko {
        color: @grey-darken;
        font-weight: normal;
      }
    }
    .contacts-location {
      color: @font-color-lighten;
    }
    .contacts-desc {
      display: none;
    }
    >i {
      cursor: pointer;
      display: none;
      position: absolute;
      top: 10px;
      right: 10px;
    }
    &:hover {
      box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.07);
      >i {
        display: block;
      }
    }
  }
  &.tags {
    transition: box-shadow 0.3s;
    position: relative;
    width: 260px;
    height: 52px;
    border: 1px solid @grey;
    border-radius: 2px;
    .contacts-avatar {
      float: left;
      width: 40px;
      height: 40px;
      margin: 6px 8px;
      border-radius: 50%;
    }
    .contacts-name {
      text-indent: 2px;
      height: 2em;
      line-height: 2em;
    }
    .contacts-location {
      margin-left: 2px;
      color: @font-color-lighten;
    }
    >i {
      cursor: pointer;
      display: none;
      position: absolute;
      top: 20px;
      right: 10px;
    }
    &:hover {
      box-shadow: @card-shadow;
      >i {
        display: block;
      }
    }
  }
  &.list {
    cursor: pointer;
    margin: 2px;
    height: 52px;
    background-color: @grey-lighten;
    .contacts-avatar {
      float: left;
      width: 40px;
      height: 40px;
      margin: 6px 8px;
      border-radius: 50%;
    }
    .contacts-name {
      text-indent: 2px;
      height: 2em;
      line-height: 2em;
    }
    .contacts-location {
      margin-left: 2px;
      color: @font-color-lighten;
    }
    >i {
      display: none;
    }
    &.current {
      background-color: @white;
    }
  }
}
</style>



