<template>
  <div class="pagination">
    <ex-button :type="curPage === 1 ? 'disabled' : 'primary'" @click="prevPage"></ex-button>
    <div class="pagination-number">
      <span>{{curPage}}</span>
      <!-- <span>/</span> -->
      <!-- <span>{{pageSize}}</span> -->
    </div>
    <!-- <ex-button :type="curPage === pageSize ? 'disabled' : 'primary'" @click="nextPage"></ex-button> -->
    <ex-button type="primary" @click="nextPage"></ex-button>
  </div>
</template>
<script>
export default {
  name: 'pagination',
  props: {
    // pageSize: {
    //   type: Number,
    //   required: true,
    // },
    curPage: {
      type: Number,
      default() {
        return 1;
      },
    },
  },
  methods: {
    prevPage() {
      this.$emit('prevPage');
    },
    nextPage() {
      this.$emit('nextPage');
    },
  },
};
</script>



