<template>
  <div class="card-list">
    <div class="title">
      <h3><i class="icon-flag"></i></h3>
      <a class="amber-text" href="javascript:;" @click="modify()">+ </a>
    </div>
    <transition-group name="list" tag="ul" @enter="enter" v-if="loading" appear>
      <li v-for="(activity, index) in activities"
      :key="activity.id"
      :data-index="index + 1"
      @click="modify(activity)">
        <activity-card :data="activity"></activity-card>
      </li>
    </transition-group>
    <loading height="450" v-else></loading>
    <place-holder v-if="activities.length === 0 && loading" prompt=""></place-holder>
  </div>
</template>
<script>
import { mapActions } from 'vuex';
import ActivityCard from '../widgets/ActivityCard';
import PlaceHolder from '../widgets/PlaceHolder';

export default {
  name: 'activities',
  props: ['id'],
  components: {
    ActivityCard,
    PlaceHolder,
  },
  computed: {
    activities() {
      return this.$store.state.activity.activities;
    },
  },
  methods: {
    ...mapActions({
      init: 'getActivities',
    }),
    modify(activity) {
      if (activity) {
        // 0
        if (activity.status === 0 || activity.status === 1) {
          this.$router.push({
            path: `/activity/newactivity?id=${activity.id}`,
          });
        } else {
          this.$router.push({
            path: `/activity/detailac?id=${activity.id}`,
          });
        }
      } else {
        this.$router.push({
          path: `/activity/newactivity?projectId=${this.id}`,
        });
      }
    },
    enter(el, done) {
      const delay = el.dataset.index * 150;
      el.style.transitionDelay = `${delay}ms`;
      el.style.WebkitTransitionDelay = `${delay}ms`;
      setTimeout(() => {
        done();
      }, delay);
    },
  },
  watch: {
    id(val) {
      this.loading = false;
      this.init(val).then(() => {
        this.loading = true;
      });
    },
  },
  created() {
    this.init(this.id).then(() => {
      this.loading = true;
    });
  },
  data() {
    return {
      loading: false,
    };
  },
};
</script>



