<template>
  <modal id="idea-detail" :click-overlay-close="false" :hide-footer="true">
    <h3 slot="header">{{config.type === 'select' ? '' : ''}}</h3>
    <div slot="body" class="detail-content">
      <div class="idea-list" v-show="config.type === 'select'">
        <div class="search">
          <i class="icon-search"></i>
          <input class="search-input" type="text" v-model="search" />
        </div>
        <!-- TODO: filter animate -->
        <!-- <transition-group name="list" tag="ul"
          @before-enter="beforeEnter"
          @enter="enter"
          @leave="leave"> -->
        <ul>
          <li v-for="(idea, index) in filIdea"
            :key="idea.id"
            :data-index="index"
            :class="{'current': curIdea && idea.id === curIdea.id }"
            @click="choose(idea)">
            {{idea.name}}
          </li>
        </ul>
        <!-- </transition-group> -->
      </div>
      <idea-detail v-if="curIdea"
        :data="curIdea"
        @selectIdea="selectIdea"
        :enable="config.type === 'select'">
      </idea-detail>
      <place-holder v-else prompt=""></place-holder>
    </div>
  </modal>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import IdeaDetail from './IdeaDetail';
import PlaceHolder from '../widgets/PlaceHolder';

export default {
  name: 'selectIdea',
  props: ['config'],
  components: {
    IdeaDetail,
    PlaceHolder,
  },
  computed: {
    ...mapGetters({
      curIdea: 'getCurIdea',
    }),
    ideas() {
      return this.$store.state.activity.ideas;
    },
    filIdea() {
      return this.ideas.filter(i =>
        i.name.toLowerCase()
        .indexOf(this.search.toLowerCase()) !== -1,
      );
    },
  },
  methods: {
    ...mapActions({
      switchIdea: 'switchIdea',
      closeModal: 'closeModal',
    }),
    choose(idea) {
      this.switchIdea(idea);
    },
    selectIdea(id) {
      this.config.callback(id);
      this.closeModal();
      this.$store.commit('RESET_IDEA');
    },
    beforeEnter(el) {
      el.style.opacity = 0;
      el.style.height = 0;
    },
    enter(el, done) {
      const delay = el.dataset.index * 150;
      setTimeout(() => {
        el.style.opacity = 1;
        el.style.height = '42px';
        done();
      }, delay);
    },
    leave(el, done) {
      const delay = el.dataset.index * 150;
      setTimeout(() => {
        el.style.opacity = 0;
        el.style.height = 0;
        done();
      }, delay);
    },
  },
  data() {
    return {
      search: '',
    };
  },
};
</script>
<style lang="less">
@import url(../../styles/variable.less);
#idea-detail {
  .modal-container {
    .modal-body {
      margin: 0;
      width: 100%;
      .detail-content {
        display: flex;
        align-items: stretch;
        .idea-list {
          width: 220px;
          min-height: 500px;
          background-color: @grey;
          border-bottom-left-radius: 2px;
          .search {
            position: relative;
            background-color: @grey-lighten;
            .search-input {
              width: 150px;
              border-radius: 17px;
              padding-left: 2em;
              padding-right: 1em;
              margin: 5px 10px;
            }
            .icon-search {
              position: absolute;
              left: 20px;
              top: 15px;
            }
          }
          >ul {
            >li {
              transition: background-color 0.3s;
              cursor: pointer;
              margin-top: 1px;
              padding-left: 16px;
              height: 42px;
              line-height: 42px;
              white-space: nowrap;
              text-overflow: ellipsis;
              background-color: @grey-lighten;
              &.current {
                padding-left: 13px;
                border-left: 3px solid @amber;
                background-color: @white;
              }
            }
          }
        }
        .place-holder {
          min-width: 500px;
        }
      }
    }
  }
}
</style>



