<template>
  <div>
    <modal v-if="alert.show" :title="alert.title" :msg="alert.msg"></modal>
    <modal v-if="confirm.show" :title="confirm.title" :msg="confirm.msg">
      <div slot="footer">
        <ex-button type="primary" @click="confirm.callback()"></ex-button>
        <ex-button></ex-button>
      </div>
    </modal>
    <new-project v-if="newProject.show" :config="newProject"></new-project>
    <select-idea v-if="selectIdea.show" :config="selectIdea"></select-idea>
    <select-we-media v-if="selectWeMedia.show" :config="selectWeMedia"></select-we-media>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import NewProject from '../activity/NewProject';
import SelectWeMedia from '../activity/SelectWeMedia';
import SelectIdea from '../activity/SelectIdea';

export default {
  components: {
    NewProject,
    SelectWeMedia,
    SelectIdea,
  },
  computed: {
    ...mapGetters({
      alert: 'getAlert',
      confirm: 'getConfirm',
      newProject: 'getNewProject',
      selectWeMedia: 'getSelectWeMedia',
      selectIdea: 'getSelectIdea',
    }),
  },
};
</script>



