import * as types from '../mutation-types';
import api from '../../services/request';

/* eslint-disable no-shadow */

const state = {
  finance: [],
  userInfo: {},
};

const getters = {};

const actions = {
  getFinance({ commit, dispatch }, queries) {
    return new Promise((resolve) => {
      api.getFinance(queries).then((response) => {
        if (response.body.code === 1) {
          if (response.body.result.length === 0) {
            dispatch('showToasts', {
              text: '',
            });
          } else {
            commit(types.RECEIVE_FINANCE, response.body.result);
            resolve();
          }
        }
      });
    });
  },
  getUserInfo({ commit }) {
    api.getUserInfo().then((response) => {
      if (response.body.code === 1) {
        commit(types.RECEIVE_USER_INFO, response.body.result);
      }
    });
  },
};

const mutations = {
  [types.RECEIVE_FINANCE](state, finance) {
    state.finance = finance;
  },
  [types.RECEIVE_USER_INFO](state, userInfo) {
    state.userInfo = userInfo;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};



