<template>
  <modal id="select-wemedia" :click-overlay-close="false" :hide-footer="true">
    <h3 slot="header"></h3>
    <div slot="body" class="detail-content">
      <div class="content-left">
        <div class="search">
          <i class="icon-search"></i>
          <input class="search-input" type="text" />
        </div>
        <!-- <a href="javascript:;">or</a> -->
        <ul class="wemedia-list">
          <li @click="choose(weMedia)" :class="{'current': weMedia.checked}" v-for="weMedia in weMediaList">
            <contacts class="list" :class="{'current': weMedia.checked}" :data="weMedia"></contacts>
          </li>
        </ul>
      </div>
      <div class="select-list">
        <div class="tags-title">
          <span> <b class="amber-text">{{count}}</b> </span>
          <a href="javascript:;" class="amber-text" @click="empty"></a>
        </div>
        <ul class="tags-list">
          <li v-for="invite in selectList">
            <contacts class="tags" :data="invite" @deselection="deselection"></contacts>
          </li>
        </ul>
        <ex-button type="primary" @click="selectWeMeida"></ex-button>
      </div>
    </div>
  </modal>
</template>
<script>
import Contacts from '../widgets/Contacts';

export default {
  name: 'selectWeMeida',
  props: ['config'],
  components: {
    Contacts,
  },
  computed: {
    count() {
      return this.selectList.length;
    },
    idList() {
      return this.selectList.map(s => s.id);
    },
    weMediaList() {
      return this.$store.state.activity.weMediaList;
    },
  },
  methods: {
    pickWeMedia() {
      this.$store.commit({
        type: 'PICK_WEMEDIA',
        idList: this.idList,
      });
    },
    empty() {
      this.selectList = [];
    },
    deselection(id) {
      this.selectList.splice(this.selectList.findIndex(i => i.id === id), 1);
    },
    choose(weMedia) {
      if (weMedia.checked) {
        this.deselection(weMedia.id);
      } else {
        this.selectList.push(weMedia);
      }
    },
    selectWeMeida() {
      this.config.callback(this.selectList);
      this.$store.dispatch('closeModal');
    },
    // TODO: search localCode
  },
  watch: {
    selectList() {
      this.pickWeMedia();
    },
  },
  data() {
    return {
      selectList: this.config.inviteList.slice(),
    };
  },
  created() {
    this.$store.dispatch({
      type: 'getWeMediaList',
      idList: this.idList,
    });
  },
};
</script>
<style lang="less">
@import url(../../styles/variable.less);
#select-wemedia {
  .modal-container {
    width: 800px;
    .modal-body {
      margin: 0;
      width: 100%;
      .detail-content {
        display: flex;
        align-items: stretch;
        .content-left {
          width: 220px;
          min-height: 500px;
          background-color: @grey;
          border-bottom-left-radius: 2px;
          .search {
            position: relative;
            background-color: @grey-lighten;
            .search-input {
              width: 150px;
              border-radius: 17px;
              padding-left: 2em;
              padding-right: 1em;
              margin: 5px 10px;
            }
            .icon-search {
              position: absolute;
              left: 20px;
              top: 15px;
            }
          }
          .wemedia-list {
            >li {
              position: relative;
              &.current:before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                width: 0;
                height: 0;
                border-top: 1em solid @amber;
                border-left: 1em solid transparent;
              }
            }
          }
        }
        .select-list {
          width: 100%;
          position: relative;
          .tags-list > li {
            margin-left: 16px;
            margin-top: 10px;
            display: inline-block;
          }
          .tags-title {
            margin: 10px 20px;
            margin-bottom: 0;
            >a {
              float: right;
            }
          }
          a.button {
            position: absolute;
            bottom: 1em;
            margin-left: 250px;
          }
        }
      }
    }
  }
}
</style>



