<template>
  <div class="project-list">
    <ul>
      <li class="ripple" :class="{'current': project.checked}" @click="choose(project)" v-for="project in projects">{{project.name}}</li>
      <li class="amber-text ripple" @click="newProject({type: 'new'})">+ </li>
    </ul>
  </div>
</template>
<script>
import { mapActions } from 'vuex';

export default {
  name: 'project',
  computed: {
    projects() {
      return this.$store.state.activity.projects;
    },
  },
  methods: {
    ...mapActions({
      init: 'getProjects',
      choose: 'switchProject',
      newProject: 'showNewProject',
    }),
  },
  created() {
    this.init();
  },
};
</script>



