import Vue from 'vue';
import 'element-ui/lib/theme-default/index.css';
// import 'element-ui/lib/theme-default/time-picker.css';
import VueResource from 'vue-resource';
import ElementUI from 'element-ui';
// import { DateTimePicker } from 'element-ui';

import God from './God';
import Modal from './components/widgets/Modal';
import Loading from './components/widgets/Loading';
import BaseButton from './components/widgets/BaseButton';

import store from './store';
import router from './router';
import timeFilter from './filters/timeFilter';
import statusFilter from './filters/statusFilter';
import adStatusFilter from './filters/adStatusFilter';
import currencyFilter from './filters/currencyFilter';
import util from './utils/util';

Vue.use(VueResource);
// TODO: 
Vue.use(ElementUI);

Vue.filter('timeFilter', timeFilter);
Vue.filter('statusFilter', statusFilter);
Vue.filter('adStatusFilter', adStatusFilter);
Vue.filter('currencyFilter', currencyFilter);

Vue.component('modal', Modal);
Vue.component('loading', Loading);
Vue.component('exButton', BaseButton);
// Vue.component(DateTimePicker.name, DateTimePicker);

Vue.http.interceptors.push((request, next) => {
  next((response) => {
    if (response.status === 200 && response.body.code !== 1) {
      const errorInfo = util.getErrorInfo(response);
      store.dispatch('showToasts', {
        text: errorInfo,
      });
    }
    if (response.status > 400) {
      if (response.status === 401) {
        store.dispatch('showConfirm', {
          title: '',
          msg: ', . ',
          show: true,
          callback: () => {
            window.location.href = '/';
          },
          cancel: () => store.dispatch('closeModal'),
        });
      } else if (response.status === 403) {
        store.dispatch('showConfirm', {
          text: '',
        });
      } else {
        store.dispatch('showToasts', {
          text: '',
        });
      }
    }
  });
});

/* eslint-disable no-new */
new Vue({
  store,
  router,
  http: {
    root: '/',
    headers: {},
  },
  render(createElement) {
    return createElement(God);
  },
}).$mount('#app');



