<template>
  <div class="project-detail">
    <div v-if="curProject">
      <div class="project-desc">
        <div>
          <h3 class="project-name">{{curProject.name}}</h3>
          <div class="project-budget">
            <table>
              <tr>
                <td>()</td>
                <td>{{curProject.budget / 1000 / 100 | currencyFilter('', 0)}}</td>
              </tr>
              <tr>
                <td>()</td>
                <td>{{frozen / 1000 / 100 | currencyFilter('', 0)}}</td>
              </tr>
              <tr>
                <td>()</td>
                <td>{{cost / 1000 / 100 | currencyFilter('', 0)}}</td>
              </tr>
            </table>
          </div>
          <a href="javascript:;" class="amber-text" @click="newProject({type: 'modify'})"></a>
        </div>
        <div class="project-memo">
          <label></label>
          <p>{{curProject.memo}}</p>
        </div>
      </div>
      <activities :id="curProject.id"></activities>
      <ideas :id="curProject.id"></ideas>
    </div>
    <place-holder v-show="!curProject" prompt=""></place-holder>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex';
import PlaceHolder from '../widgets/PlaceHolder';
import Activities from './Activities';
import Ideas from './Ideas';

export default {
  name: 'detail',
  components: {
    PlaceHolder,
    Activities,
    Ideas,
  },
  computed: {
    ...mapGetters({
      curProject: 'getCurProject',
      frozen: 'frozen',
      cost: 'cost',
    }),
  },
  methods: {
    ...mapActions({
      newProject: 'showNewProject',
    }),
  },
};
</script>
<style lang="less">
@import url(../../styles/variable.less);
.project-detail {
  width: 790px;
  min-height: 500px;
  float: right;
  background-color: @grey;
  .project-desc {
    position: relative;
    padding: 20px;
    background-color: @white;
    >div {
      display: flex;
      >a {
        padding: 0 0.5em;
      }
    }
    .project-name {
      flex-grow: 1;
      height: 1em;
    }
    .project-budget {
      position: relative;
      width: 140px;
      > table {
        position: absolute;
        height: 1em;
        right: 0;
        white-space: nowrap;
        width: auto;
        margin: 0;
        border: 0;
        text-align: left;
        td {
          &:first-child {
            text-align: right;
          }
        }
      }
    }
    .project-memo {
      margin-top: 20px;
      >label {
        flex-shrink: 0;
      }
      >p {
        margin: 0;
        word-break: break-word;
        line-height: 1.5em;
        height: 4.5em;
      }
    }
  }
  .card-list {
    float: left;
    width: 375px;
    margin-left: 14px;
    margin-top: 30px;
    .title {
      display: flex;
      >h3 {
        flex-grow: 1;
        >i {
          margin: 0 5px;
        }
      }
      >a {
        padding: 0 0.5em;
        line-height: 24px;
      }
    }
    .card-wrapper {
      transition: box-shadow 0.3s;
      cursor: pointer;
      display: flex;
      flex-wrap: wrap;
      padding: 15px;
      background-color: @white;
      border-radius: 3px;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
      &:hover {
        box-shadow: 2px 4px 4px 0 rgba(0, 0, 0, 0.14);
      }
      dd {
        margin: 0;
        display: inline-block;
        &.card-row {
          margin: 1em 0;
          height: 4.2em;
          line-height: 1.4em;
          overflow: hidden;
          text-overflow: ellipsis;
          word-break: break-all;
        }
      }
      dt {
        margin: 0;
        display: inline-block;
      }
      .card-row {
        display: flex;
        width: 100%;
        >h3 {
          flex-grow: 1;
        }
      }
      .card-row-half {
        width: 50%;
        line-height: 2em;
      }
      .card-status {
        display: block;
        min-width: 3em;
        padding: 0 0.5em;
        height: 20px;
        line-height: 20px;
        text-align: center;
        border-radius: 2px;
      }
    }
  }
}
.list-enter-active {
  transition: transform 0.5s, opacity 0.5s;
}
.list-enter {
  position: absolute;
  opacity: 0;
  transform: translateY(20px);
}
</style>



