<template>
  <div id="idea-form">
    <div class="title">
      <router-link class="amber-text icon-arrow-right" to="/activity"></router-link>
      <h3>{{curIdea ? '' : ''}}</h3>
    </div>
    <idea-form :data="id ? curIdea : undefined"></idea-form>
  </div>
</template>
<script>
import IdeaForm from './IdeaForm';

export default {
  name: 'newIdea',
  components: {
    IdeaForm,
  },
  computed: {
    curIdea() {
      return this.$store.state.activity.ideas.find(i => i.id === this.id);
    },
  },
  data() {
    return {
      id: parseInt(this.$route.query.id, 10),
    };
  },
};
</script>
<style lang="less">
@import url(../../styles/variable.less);
#idea-form {
  .idea-img-list {
    padding-left: 73px;
    display: flex;
    flex-wrap: wrap;
    .idea-img-item {
      position: relative;
      width: 100px;
      height: 100px;
      margin-right: 10px;
      margin-top: 10px;
      .icon-close-small {
        position: absolute;
        z-index: 1;
        top: -6px;
        right: -6px;
        padding-top: 1px;
        border-radius: 50%;
        background-color: @grey;
        border: 3px solid transparent;
        &:hover {
          background-color: @red;
          &:before,
          &:after {
            background-color: @white;
          }
        }
      }
      .img-overlay {
        position: absolute;
        width: 100px;
        height: 100px;
        line-height: 100px;
        text-align: center;
        font-size: 16px;
        font-weight: bold;
        text-shadow: 0 0 15px @white;
        color: @black;
      }
      .img-mask {
        cursor: pointer;
        position: absolute;
        width: 100px;
        height: 100px;
        line-height: 100px;
        border-radius: 5px;
        color: @white;
        background-color: rgba(0, 0, 0, 0.7);
        font-size: 16px;
        text-align: center;
        overflow: hidden;
      }
      img {
        width: 100px;
        height: 100px;
        border-radius: 5px;
      }
    }
  }
}
</style>



