import Vue from 'vue';

export default {
  getIdeas(id) {
    const url = `/god/creative/list?projectId=${id}`;
    return Vue.http.get(url);
  },
  getProjects() {
    const url = '/god/project/list?pageIndex=1&number=50';
    return Vue.http.get(url);
  },
  getActivities(id) {
    const url = `/god/activity/list?projectId=${id}`;
    return Vue.http.get(url);
  },
  createProject(p) {
    const URL = '/god/project/create';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        name: p.name,
        budget: p.budget,
        memo: p.memo,
      },
    });
  },
  createActivity(a) {
    const URL = '/god/activity/create';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        projectId: a.projectId,
        name: a.name,
        budget: a.budget,
        desc: a.desc,
        minPrice: a.minPrice,
        maxPrice: a.maxPrice,
        memo: a.memo,
        startTime: a.startTime,
        // endTime: a.endTime,
        beInviteUser: a.beInviteUser,
        creativeId: a.creativeId,
      },
    });
  },
  createIdea(i) {
    const URL = '/god/creative/create';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        projectId: i.projectId,
        name: i.name,
        desc: i.desc,
        requires: i.requires,
        picIds: i.picIds,
        videoUrl: i.videoUrl,
      },
    });
  },
  updateProject(p) {
    const URL = '/god/project/update';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        id: p.id,
        name: p.name,
        budget: p.budget,
        memo: p.memo,
      },
    });
  },
  updateIdea(i) {
    const URL = '/god/creative/update';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        projectId: i.projectId,
        name: i.name,
        desc: i.desc,
        status: i.status,
        requires: i.requires,
        picIds: i.picIds,
        videoUrl: i.videoUrl,
        userId: i.userId,
        id: i.id,
        type: i.type,
      },
    });
  },
  updateActivity(a) {
    const URL = '/god/activity/update';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        id: a.id,
        projectId: a.projectId,
        name: a.name,
        budget: a.budget,
        desc: a.desc,
        minPrice: a.minPrice,
        maxPrice: a.maxPrice,
        memo: a.memo,
        startTime: a.startTime,
        // endTime: a.endTime,
        beInviteUser: a.beInviteUser,
        creativeId: a.creativeId,
        userId: a.userId,
        status: a.status,
      },
    });
  },
  publishActivity(a) {
    const URL = '/god/activity/publish';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        id: a.id,
        projectId: a.projectId,
        name: a.name,
        budget: a.budget,
        desc: a.desc,
        minPrice: a.minPrice,
        maxPrice: a.maxPrice,
        memo: a.memo,
        startTime: a.startTime,
        // endTime: a.endTime,
        beInviteUser: a.beInviteUser,
        creativeId: a.creativeId,
        userId: a.userId,
        status: a.status,
      },
    });
  },
  endActivity(activityId) {
    return Vue.http.post(`/god/activity/end?activityId=${activityId}`);
  },
  uploadImage(image) {
    const URL = '/god/creative/pic/upload';
    return Vue.http({
      url: URL,
      method: 'POST',
      params: {
        file: image,
      },
    });
  },
  getActivityInfo(activityId) {
    return Vue.http.get(`/common/p/activity/info?activityId=${activityId}`);
  },
  getTasksInfo(activityId) {
    return Vue.http.get(`/god/activity/tasks/info?activityId=${activityId}`);
  },
  logout() {
    return Vue.http.post('/logout');
  },
  getWeMediaList(locationCode = 0) {
    return Vue.http.get(`/common/account/list?locationCode=${locationCode}`);
  },
  getDraftInfo(activityId) {
    return Vue.http.get(`/god/activity/info?activityId=${activityId}`);
  },
  getStatis(type = 'user', queries) {
    return Vue.http.get(`/god/${type}/statis`, {
      params: {
        st: queries.startTime,
        et: queries.endTime,
        name: queries.name,
        page: queries.page,
        number: 50,
      },
    });
  },
  getFinance(queries) {
    return Vue.http.get('/common/finance/log', {
      params: {
        st: queries.startTime,
        et: queries.endTime,
        page: queries.page,
        number: 50,
      },
    });
  },
  getUserInfo() {
    return Vue.http.get('/common/user/info');
  },
  operateTask(operate, taskId) {
    return Vue.http.post(`/god/task/event/${operate}?taskId=${taskId}`);
  },
};



