<template>
  <div class="idea-detail">
    <div class="text-field" v-show="!enable">
      <label></label>
      <p>{{data.name}}</p>
    </div>
    <div class="text-field">
      <label></label>
      <p :class="statusMap[data.status]">{{data.status | statusFilter('i')}}</p>
    </div>
    <div class="text-field">
      <label></label>
      <p>{{data.desc}}</p>
    </div>
    <div class="text-field">
      <label></label>
      <p>{{data.requires || ''}}</p>
    </div>
    <div class="text-field">
      <label></label>
      <div class="img-list" v-if="data.picIds">
        <img v-for="img in renderImg" :src="img" v-if="img" />
      </div>
      <p v-else></p>
    </div>
    <div class="text-field">
      <label></label>
      <p>{{data.videoUrl || ''}}</p>
    </div>
    <div class="text-field" v-if="data.status === 1">
      <label></label>
      <p>{{data.reason || ''}}</p>
    </div>
    <ex-button type="primary" v-show="enable" @click="selectIdea"></ex-button>
  </div>
</template>
<script>
export default {
  name: 'ideaDetail',
  props: ['data', 'enable'],
  computed: {
    renderImg() {
      return this.data.picIds.split(',');
    },
  },
  methods: {
    selectIdea() {
      this.$emit('selectIdea', this.data.id);
    },
  },
  data() {
    return {
      statusMap: {
        0: 'indigo-text',
        1: 'red-text',
        2: 'teal-text',
      },
    };
  },
};
</script>
<style lang="less">
.idea-detail {
  width: 548px;
  padding: 10px 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  >.text-field .img-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 330px;
    img {
      width: 100px;
      height: 100px;
      border-radius: 5px;
      margin-right: 10px;
      margin-top: 10px;
    }
  }
  >a.button {
    margin-top: 1em;
    margin-left: 250px;
  }
}
</style>



