<template>
  <div>
    <navbar :items="navItems"></navbar>
    <transition name="fade" mode="out-in">
      <router-view class="main-content clear-fix"></router-view>
    </transition>
    <modal-manage></modal-manage>
    <toasts></toasts>
  </div>
</template>
<script>
import Navbar from './components/widgets/Navbar';
import Toasts from './components/widgets/Toasts';
import ModalManage from './components/widgets/ModalManage';

export default {
  components: {
    Navbar,
    Toasts,
    ModalManage,
  },
  data() {
    return {
      navItems: [{
        name: '',
        location: '/activity',
      }, {
        name: '',
        location: '/data',
      }, {
        name: '',
        location: '/account',
      }, {
        name: '',
        location: '/setting',
      }],
    };
  },
};
</script>
<style lang="less">
@import url('./styles/variable.less');
@import url('./styles/reset.less');
@import url('./styles/global-class.less');
@import url('./styles/icon.less');
@import url('./styles/button.css');
@import url('./styles/toasts.less');
@import url('./styles/place-holder.less');
@import url('./styles/project.less');
.main-content {
  width: 1000px;
  margin: 59px auto 7px;
}

.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s cubic-bezier(0.42, 0, 0.58, 1);
}

.fade-enter,
.fade-leave-active {
  transform: translateY(20px);
  opacity: 0;
}

.pagination {
  height: 33px;
  width: 100%;
  display: flex;
  justify-content: center;
  .pagination-number {
    width: 80px;
    height: 33px;
    line-height: 33px;
    text-align: center;
  }
}

.tabs-content {
  width: 100%;
  display: flex;
  background-color: @grey;
  >li {
    transition: background-color 0.3s;
    cursor: pointer;
    width: 120px;
    height: 42px;
    line-height: 42px;
    text-align: center;
    &:hover {
      opacity: 0.7;
    }
    &.current {
      pointer-events: none;
      background-color: @white;
    }
  }
}

.el-date-picker__header-label.active,
.el-date-picker__header-label:hover,
.el-picker-panel__icon-btn:hover {
  color: @amber;
}

.el-date-table td.in-range {
  background-color: #fff5e8;
}

.el-date-table td.in-range:hover {
  background-color: #ffd9a3;
}

.el-date-table td.current:not(.disabled),
.el-date-table td.end-date,
.el-date-table td.start-date {
  background-color: @amber!important;
  color: @white;
}

.el-date-table td.today {
  color: @amber;
}

.el-date-table td.today:before {
  border-top: 0.5em solid @amber;
}

.el-picker-panel__link-btn {
  color: @amber;
}

.el-time-panel__btn.confirm {
  color: @amber;
}

.el-time-panel__content::after,
.el-time-panel__content::before {
  background-color: @amber;
}

.el-input__inner {
  height: 34px!important;
  line-height: 34px!important;
  font-size: 14px!important;
}
</style>



