const statusMap = {
  '-1': '',
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '',
  7: '',
  8: '',
  71: '',
  72: '',
  73: '',
  74: '',
};

export default function getAdStatusStr(adStatus, taskStatus, reason) {
  switch (adStatus) {
    case 0:
      return '';
    case 1:
      return statusMap[taskStatus];
    case 2:
      return ` (: ${reason})`;
    default:
      break;
  }
  return '';
}



