<template>
  <div class="activity-detail" v-if="loading">
    <div class="title">
      <router-link class="amber-text icon-arrow-right" to="/activity"></router-link>
      <h3>{{curActivity.name}}<span :class="statusMap[curActivity.status]">{{curActivity.status | statusFilter}}</span></h3>
      <ex-button v-show="curActivity.status < 4" type="primary" @click="closeActivity"></ex-button>
    </div>
    <table>
      <thead>
        <tr>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
          <th>()</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{{curActivity.budget / 1000 / 100 | currencyFilter('', 0) || '0'}}</td>
          <td>{{totalCost.totalPay / 1000 / 100 | currencyFilter('') || '0'}}</td>
          <td>{{totalCost.impNum || '0'}}</td>
          <td>{{totalCost.clickNum || '0'}}</td>
          <td>{{(totalCost.totalPay / totalCost.impNum * 1000 / 1000 / 100) | currencyFilter('') || '0'}}</td>
          <td>{{(totalCost.totalPay / totalCost.clickNum / 1000 / 100) | currencyFilter('') || '0'}}</td>
        </tr>
      </tbody>
    </table>
    <div>
      <div class="text-field">
        <label></label>
        <p>{{curActivity.desc}}</p>
      </div>
      <div class="text-field">
        <label></label>
        <span>{{curActivity.startTime | timeFilter}}</span>
        <a href="javascript:;" class="amber-text" v-if="!timeFlag" v-show="curActivity.status === 2" @click="modify('time')"></a>
        <div class="modify-field" v-else>
          <el-date-picker v-model="startTime" name="time" type="datetime" placeholder=""></el-date-picker>
          <ex-button type="primary" @click="confirm('time')"></ex-button>
          <ex-button @click="timeFlag = false"></ex-button>
        </div>
      </div>
      <div class="text-field">
        <label></label>
        <span>{{curActivity.budget / 1000 / 100 | currencyFilter('', 0) || '0'}}</span>
        <a href="javascript:;" class="amber-text" v-if="!budgetFlag" @click="modify('budget')"></a>
        <div class="modify-field" v-else>
          <input type="number" name="budget" v-model.number="budget" step="100" />
          <ex-button type="primary" @click="confirm('budget')"></ex-button>
          <ex-button @click="budgetFlag = false"></ex-button>
        </div>
      </div>
      <div class="text-field">
        <label></label>
        <span>{{curActivity.minPrice / 100}}</span>~
        <span>{{curActivity.maxPrice / 100}}</span>/CPM
        <a href="javascript:;" class="amber-text" v-if="!priceFlag" @click="modify('price')"></a>
        <div class="modify-field" v-else>
          <input type="number" name="minPrice" class="small" v-model.number="minPrice" />
          <span>-</span>
          <input type="number" name="maxPrice" class="small" v-model.number="maxPrice" />
          <ex-button type="primary" @click="confirm('price')"></ex-button>
          <ex-button @click="priceFlag = false"></ex-button>
        </div>
      </div>
      <div class="text-field">
        <label></label>
        <div class="idea-list" v-for="creative in detail.creatives">
          <span>{{creative.name}}</span>
          <a href="javascript:;" class="amber-text" @click="inspect(creative)"></a>
        </div>
      </div>
      <div class="text-field">
        <label></label>
        <span>{{curActivity.memo || ''}}</span>
      </div>
    </div>
    <div class="wemedia-list" v-if="detail.invitations.length !== 0">
      <div class="wemedia-header">
        <h3></h3>
        <!-- <a href="javascript:;" class="amber-text"></a> -->
        <!-- <a href="javascript:;" class="amber-text"></a> -->
      </div>
      <ul>
        <li v-for="invite in detail.invitations">
          <we-media :data="invite" :task="taskMap[invite.id]" :cost="costMap[invite.id]"></we-media>
        </li>
      </ul>
    </div>
  </div>
  <loading v-else></loading>
</template>
<script>
import moment from 'moment';
import { mapActions } from 'vuex';
import WeMedia from '../widgets/WeMedia';
import util from '../../utils/util';

export default {
  name: 'activityDetail',
  components: {
    WeMedia,
  },
  computed: {
    detail() {
      return this.$store.state.activity.activityInfo;
    },
    curActivity() {
      return this.$store.state.activity.activities.find(a => a.id === this.id);
    },
    taskMap() {
      const taskMap = {};
      const tasks = this.curActivity.tasks;
      this.detail.invitations.forEach((i) => {
        taskMap[i.id] = tasks.find(t => i.id === t.invitationId);
      });
      return taskMap;
    },
    costMap() {
      const costMap = {};
      const tasks = this.curActivity.tasks;
      const costList = this.curActivity.taskCostList;
      this.detail.invitations.forEach((i) => {
        const task = tasks.find(t => t.invitationId === i.id);
        if (task) {
          costMap[i.id] = costList.find(c => c.id === task.adPlatformTaskId);
        } else {
          costMap[i.id] = undefined;
        }
      });
      return costMap;
    },
    totalCost() {
      return util.sumObject(this.curActivity.taskCostList);
    },
  },
  methods: {
    ...mapActions({
      getActivityInfo: 'getActivityInfo',
      updateActivity: 'updateActivity',
      switchIdea: 'switchIdea',
      showConfirm: 'showConfirm',
      showSelectIdea: 'showSelectIdea',
      endActivity: 'endActivity',
      closeModal: 'closeModal',
    }),
    modify(type) {
      ['budget', 'price', 'time'].forEach((f) => {
        if (f === type) {
          this[`${f}Flag`] = true;
        } else {
          this[`${f}Flag`] = false;
        }
      });
    },
    confirm(type) {
      const params = {
        id: this.detail.activity.id,
        projectId: this.detail.activity.projectId,
        userId: this.detail.activity.userId,
        status: this.detail.activity.status,
        budget: this.detail.activity.budget,
        minPrice: this.detail.activity.minPrice,
        maxPrice: this.detail.activity.maxPrice,
      };
      if (type === 'price') {
        params.minPrice = this.minPrice * 100;
        params.maxPrice = this.maxPrice * 100;
      } else if (type === 'budget') {
        if ((this.budget * 1000 * 100) - this.curActivity.budget < 10 * 1000 * 100) {
          this.$store.dispatch('showToasts', {
            text: '10 ',
          });
          return;
        }
        params.budget = this.budget * 1000 * 100;
      }
      if (type === 'time') {
        const now = new Date().valueOf();
        const value = new Date(this.startTime).valueOf();
        if (value - now < 3600000) {
          this.$store.dispatch('showToasts', {
            text: '',
          });
          return;
        }
        params.startTime = `${moment(this.startTime).format('YYYY-MM-DD HH:mm')}:00`;
      }
      this.updateActivity(params).then(() => {
        this[`${type}Flag`] = false;
      });
    },
    inspect(idea) {
      this.switchIdea(idea).then(() => {
        this.showSelectIdea({ type: 'inspect' });
      });
    },
    closeActivity() {
      this.showConfirm({
        title: '',
        msg: '',
        show: true,
        callback: () => {
          this.closeModal();
          this.endActivity(this.id);
        },
      });
    },
  },
  created() {
    this.getActivityInfo(this.id).then(() => {
      this.budget = this.curActivity.budget / 1000 / 100;
      this.minPrice = this.curActivity.minPrice / 100;
      this.maxPrice = this.curActivity.maxPrice / 100;
      this.loading = true;
    });
  },
  data() {
    return {
      id: parseInt(this.$route.query.id, 10),
      loading: false,
      startTime: '',
      timeFlag: false,
      budget: 0,
      budgetFlag: false,
      minPrice: 0,
      maxPrice: 0,
      priceFlag: false,
      statusMap: {
        0: 'amber-text',
        1: 'purple-text',
        2: 'indigo-text',
        3: 'teal-text',
        4: 'amber-text',
        5: 'red-text',
      },
    };
  },
};
</script>
<style lang="less">
.activity-detail {
  >.title >h3 >span {
    vertical-align: top;
    margin-left: 1em;
    padding: 1px 2px;
    font-size: 12px;
    border: 1px solid;
    border-radius: 2px;
  }
  .idea-list {
    display: inline-block;
    >a {
      margin-left: 3em;
    }
  }
  .wemedia-list {
    margin-top: 10px;
    .wemedia-header {
      >h3 {
        display: inline-block;
      }
      >a {
        float: right;
        margin-left: 1em;
      }
    }
  }
  .modify-field {
    display: inline-flex;
    vertical-align: middle;
    >input {
      font-size: 12px;
      height: 2em;
      margin: 0 10px;
      &.small {
        width: 3em;
      }
    }
    .el-input {
      font-size: 12px;
      height: 26px;
      margin: 0 10px;
      .el-input__inner {
        height: 26px!important;
      }
    }
    >span {
      height: 2em;
      line-height: 2em;
      font-size: 12px;
    }
    >a.button {
      font-size: 13px;
      width: 60px;
      height: 2em;
      line-height: 2em;
      &+a.button {
        margin-left: 10px;
      }
    }
  }
}
</style>



