import * as types from '../mutation-types';

/* eslint-disable no-shadow */

const state = {
  config: {
    show: false,
    text: '',
    duration: 3000,
  },
};
const mutations = {
  [types.SHOW_TOASTS](state, text) {
    state.config.show = true;
    state.config.text = text;
  },
  [types.HIDE_TOASTS](state) {
    state.config.show = false;
  },
};
const actions = {
  showToasts({ commit }, { text, duration = 1500 }) {
    commit(types.SHOW_TOASTS, text);
    setTimeout(() => {
      commit(types.HIDE_TOASTS);
    }, duration);
  },
};

export default {
  state,
  actions,
  mutations,
};



