<template>
  <dl class="card-wrapper ripple">
    <dt class="card-row">
      <h3>{{data.name}}</h3>
      <span class="card-status" :class="statusMap[data.status]">{{data.status | statusFilter('i')}}</span>
    </dt>
    <dd class="card-row">{{data.desc}}</dd>
  </dl>
</template>
<script>
export default {
  name: 'ideaCard',
  props: ['data'],
  data() {
    return {
      statusMap: {
        0: 'indigo',
        1: 'red',
        2: 'teal',
      },
    };
  },
};
</script>



