import * as types from '../mutation-types';
import api from '../../services/request';

/* eslint-disable no-shadow */

const state = {
  tabs: [{
    name: '',
    type: 'user',
    checked: true,
  }, {
    name: '',
    type: 'project',
    checked: false,
  }, {
    name: '',
    type: 'activity',
    checked: false,
  }],
  statis: [],
};

const getters = {
  curTab: state => state.tabs.find(t => t.checked),
};

const actions = {
  getStatis({ commit, dispatch }, queries) {
    return new Promise((resolve) => {
      api.getStatis(queries.type, queries).then((response) => {
        if (response.body.code === 1) {
          if (response.body.result.length === 0) {
            dispatch('showToasts', {
              text: '',
            });
          } else {
            commit(types.RECEIVE_STATIS, response.body.result);
            resolve();
          }
        }
      });
    });
  },
};

const mutations = {
  [types.SWITCH_TAB](state, { tab }) {
    state.tabs.forEach((t) => {
      if (tab === t) {
        t.checked = true;
      } else {
        t.checked = false;
      }
    });
  },
  [types.RECEIVE_STATIS](state, statis) {
    state.statis = statis;
  },
};

export default {
  state,
  getters,
  actions,
  mutations,
};



